'use client';

import { useEffect, useState } from 'react';
import { usePathname } from 'next/navigation';

export default function Pool() {
  const pathname = usePathname(); // e.g., "/pool/cmh564sbg0000twlk1qj28fx1"
  const [showFallback, setShowFallback] = useState(false);
  const [countdown, setCountdown] = useState(3);

  useEffect(() => {
    // Try to open the app immediately
    tryOpenApp();

    // Countdown timer
    const countdownInterval = setInterval(() => {
      setCountdown((prev) => {
        if (prev <= 1) {
          clearInterval(countdownInterval);
          setShowFallback(true);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(countdownInterval);
  }, [pathname]);

  const tryOpenApp = () => {
    const userAgent = navigator.userAgent.toLowerCase();
    const isAndroid = userAgent.indexOf('android') > -1;
    const isIOS = /iphone|ipad|ipod/.test(userAgent);

    console.log('Opening app with path:', pathname);

    if (isAndroid) {
      // Method 1: Android Intent URI - most reliable
      const intentUri = `intent:/${pathname}#Intent;scheme=blotopay;package=com.yassirira.newapp;end`;
      console.log('Android Intent:', intentUri);
      window.location.href = intentUri;

      // Method 2: Fallback to custom scheme after 1 second
      setTimeout(() => {
        const customScheme = `blotopay:/${pathname}`;
        console.log('Fallback to custom scheme:', customScheme);
        window.location.href = customScheme;
      }, 1000);

    } else if (isIOS) {
      // iOS custom scheme
      const customScheme = `blotopay:/${pathname}`;
      console.log('iOS custom scheme:', customScheme);
      window.location.href = customScheme;

    } else {
      // Desktop - show fallback immediately
      setShowFallback(true);
      setCountdown(0);
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.card}>
        {/* App Icon */}
        <div style={styles.iconContainer}>
          <div style={styles.icon}>üí∞</div>
        </div>

        {/* Title */}
        <h1 style={styles.title}>Opening BlotoPay</h1>

        {/* Loading State */}
        {!showFallback ? (
          <div style={styles.loadingContainer}>
            <div style={styles.spinner}></div>
            <p style={styles.message}>
              Redirecting you to the app...
            </p>
            <p style={styles.countdown}>{countdown}s</p>
          </div>
        ) : (
          // Fallback Options
          <div style={styles.fallbackContainer}>
            <p style={styles.fallbackMessage}>
              App didn't open automatically?
            </p>

            {/* Try Again Button */}
            <button onClick={tryOpenApp} style={styles.primaryButton}>
              üöÄ Open in BlotoPay App
            </button>

            {/* Divider */}
            <div style={styles.divider}>
              <span style={styles.dividerText}>or</span>
            </div>

            {/* Download Section */}
            <div style={styles.downloadSection}>
              <p style={styles.downloadText}>Don't have the app yet?</p>
              <div style={styles.storeButtons}>
                <a
                  href="https://play.google.com/store/apps/details?id=com.yassirira.newapp"
                  target="_blank"
                  rel="noopener noreferrer"
                  style={styles.storeButton}
                >
                  <span style={styles.storeBadge}>üì±</span>
                  <div>
                    <div style={styles.storeLabel}>Get it on</div>
                    <div style={styles.storeName}>Google Play</div>
                  </div>
                </a>
                
                {/* Uncomment when iOS app is ready */}
                {/* <a
                  href="YOUR_IOS_APP_STORE_LINK"
                  target="_blank"
                  rel="noopener noreferrer"
                  style={styles.storeButton}
                >
                  <span style={styles.storeBadge}>üçé</span>
                  <div>
                    <div style={styles.storeLabel}>Download on the</div>
                    <div style={styles.storeName}>App Store</div>
                  </div>
                </a> */}
              </div>
            </div>

            {/* Pool ID Info */}
            <div style={styles.infoBox}>
              <p style={styles.infoText}>
                <strong>Pool Link:</strong>
              </p>
              <code style={styles.codeBlock}>{pathname}</code>
            </div>
          </div>
        )}
      </div>

      {/* Footer */}
      <div style={styles.footer}>
        <p style={styles.footerText}>BlotoPay - Secure Payment Pools</p>
      </div>
    </div>
  );
}

const styles = {
  container: {
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column' ,
    alignItems: 'center',
    justifyContent: 'center',
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    padding: '20px',
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
  },
  card: {
    background: 'white',
    padding: '48px 32px',
    borderRadius: '24px',
    boxShadow: '0 20px 60px rgba(0,0,0,0.3)',
    textAlign: 'center' ,
    maxWidth: '480px',
    width: '100%',
  },
  iconContainer: {
    marginBottom: '24px',
  },
  icon: {
    width: '96px',
    height: '96px',
    margin: '0 auto',
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    borderRadius: '24px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: '48px',
    boxShadow: '0 8px 24px rgba(102, 126, 234, 0.3)',
  },
  title: {
    color: '#1a1a1a',
    fontSize: '32px',
    fontWeight: '700' ,
    marginBottom: '24px',
    margin: '0 0 24px 0',
  },
  loadingContainer: {
    padding: '24px 0',
  },
  spinner: {
    border: '4px solid #f3f3f3',
    borderTop: '4px solid #667eea',
    borderRadius: '50%',
    width: '64px',
    height: '64px',
    animation: 'spin 1s linear infinite',
    margin: '0 auto 24px',
  },
  message: {
    color: '#666',
    fontSize: '18px',
    marginBottom: '16px',
  },
  countdown: {
    color: '#667eea',
    fontSize: '24px',
    fontWeight: '600' ,
  },
  fallbackContainer: {
    padding: '16px 0',
  },
  fallbackMessage: {
    color: '#666',
    fontSize: '16px',
    marginBottom: '24px',
  },
  primaryButton: {
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    color: 'white',
    border: 'none',
    padding: '16px 32px',
    fontSize: '18px',
    fontWeight: '600' ,
    borderRadius: '12px',
    cursor: 'pointer',
    width: '100%',
    transition: 'transform 0.2s, box-shadow 0.2s',
    boxShadow: '0 4px 12px rgba(102, 126, 234, 0.3)',
  },
  divider: {
    display: 'flex',
    alignItems: 'center',
    margin: '32px 0',
  },
  dividerText: {
    color: '#999',
    padding: '0 16px',
    fontSize: '14px',
    width: '100%',
    textAlign: 'center' ,
  },
  downloadSection: {
    marginBottom: '24px',
  },
  downloadText: {
    color: '#666',
    fontSize: '16px',
    marginBottom: '16px',
  },
  storeButtons: {
    display: 'flex',
    flexDirection: 'column' ,
    gap: '12px',
  },
  storeButton: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: '12px',
    padding: '14px 24px',
    background: '#f8f9fa',
    borderRadius: '12px',
    textDecoration: 'none',
    color: '#1a1a1a',
    transition: 'background 0.2s',
    border: '2px solid #e9ecef',
  },
  storeBadge: {
    fontSize: '32px',
  },
  storeLabel: {
    fontSize: '12px',
    color: '#666',
    textAlign: 'left' ,
  },
  storeName: {
    fontSize: '16px',
    fontWeight: '600' ,
    textAlign: 'left' ,
  },
  infoBox: {
    background: '#f8f9fa',
    padding: '16px',
    borderRadius: '12px',
    marginTop: '24px',
  },
  infoText: {
    color: '#666',
    fontSize: '14px',
    margin: '0 0 8px 0',
  },
  codeBlock: {
    display: 'block',
    background: 'white',
    padding: '12px',
    borderRadius: '8px',
    fontSize: '13px',
    color: '#667eea',
    wordBreak: 'break-all' ,
    border: '1px solid #e9ecef',
  },
  footer: {
    marginTop: '32px',
  },
  footerText: {
    color: 'rgba(255, 255, 255, 0.8)',
    fontSize: '14px',
  },
};